import React from 'react'

export default function NumberLine() {
    return (
        <section>
            <div>
            <div class='d-flex'>
                <span class='fw-bold fs-1'>{`<`}</span>
                <div class='75'>
                    <div class='line'></div>
                </div>
                <span class='fw-bold fs-1'>{`>`}</span>
            </div> 
            </div>
        </section>
    )
}
